module.exports = "const vuexMutations = {\r\n    namespaced: true,\r\n    state: {\r\n        count: 0\r\n    },\r\n    mutations: {\r\n        increment(state, payload){\r\n            state.count += payload.num\r\n        }\r\n    },\r\n    getters: {\r\n        count: state => 'count: ' + state.count\r\n    }\r\n}\r\n\r\nexport default vuexMutations\r\n";